body {
  background-color: #181a1b;
  color: #ffffff;
}

.App {
  padding-top: 3em;
  height: calc(100vh - 3em);
  max-width: 100vw;
}

.content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.challengeCard {
  max-width: 700px;
  border-radius: 0.2em;
  box-shadow: 3px 3px 10px black;
  width: calc(30% - 2em);
  background-color: #424242;
  margin: 0.2em 0em;
  padding: 0.5em;
  padding-right: 5em;
  display: flex;
  background-repeat: no-repeat;
  background-position-x: 100%;
  background-position-y: 50%;
  background-size: 5em;
  cursor: pointer;
  transition: all 250ms ease-in-out;
}

.challengeCard.done {
  color: rgba(255, 255, 255, 0.2);
  background-color: #262626;
}

.challengeCard.master {
  border-top: 5px solid darkgoldenrod;
}

.challengeCard.killer {
  background-image: url("./images/killer.png");
}

.challengeCard.survivor {
  background-image: url("./images/survivor.png");
}

.challengeCard.shared {
  background-image: url("./images/shared.png");
}

.challengeCol {
  flex-shrink: 20;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  flex-direction: column;
}

.rewardsCol {
  width: 5.5em;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  flex-direction: column;
}

.rewardIcon {
  height: 1.5em;
}

.rewardRow {
  display: flex;
  align-items: center;
}

.appBar {
  z-index: 10;
  height: 2.75em;
  padding: 0em 1em;
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  background-color: rgb(112, 21, 22);
  position: fixed;
  width: 100%;
  top: 0px;
  box-shadow: 3px 3px 10px black;
}

.appTitle {
  text-transform: uppercase;
}

.appBar > * {
  flex-shrink: 0;
  margin-right: 1em;
}
.appBar > :last-child {
  margin-right: 2em;
}

.appBar .appLogo {
  height: 100%;
  margin-right: 0.5em;
}

.completed {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.completed > img {
  max-height: calc(100vh - 3em);
}

@media screen and (orientation: portrait) {
  .completed > img {
    max-height: none;
    max-width: 100vw;
  }
}

.checkBoxContainer {
  display: flex;
  align-items: center;
}

.checkBoxContainer > label {
  text-transform: uppercase;
  font-size: 0.7em;
  font-weight: 600;
}

input[type="text"] {
  border-radius: 3px;
  padding: 10px;
  background-color: #181a1b;
  border: none;
  color: #fff;
  width: 12em;
  transition: all 250ms ease-in-out;
}

input[type="text"]:focus {
  width: 24em;
}

.appBar .spacer {
  flex-shrink: 1;
  width: 100%;
}

@media screen and (max-width: 1201px) {
  .challengeCard {
    width: calc(100vw - 7em);
  }
}
